// 로그인, 회원 가입, 회원 탈퇴
import { instance } from './index';

// 회원가입 API
function SignupUser(userData) {
	return instance.post('http://localhost:80/signup', userData);
}

// 약관동의 API
function MemberAgreeUser(userData) {
	return instance.post('MemberAgree', userData);
}

// 로그인 API
function SigninUser(userData) {
	return instance.post('http://localhost:80/signin', userData);
}

export { SignupUser, SigninUser, MemberAgreeUser };
